<template>
    <input type="text" v-model="nn"><br>
    <input type="number" v-model.number="numberModel" placeholder="숫자를 입력해주세요.">
    <p>입력된 숫자는 {{ numberModel }} 입니다.</p>
    <div v-html="htmlString"></div>
    <h1>
        Hello,World <br><br>
        {{ title }}
    </h1>
    <p> {{count}}회</p>
	<button @click="countUp">카운터</button>
</template>
<script>
export default{
    data() {
        return{//vue cdn 방식에서 data랑 같은 역할
            title:'뷰에 오신것을 환영합니다',
            count:0,
            htmlString:'<h2>HTML문자를 렌더링 합니다.</h2>',//landering : 해당 문자열에 포함된 태그를 실제 적용시켜줌
            numberModel:3,
            nn:'안녕하세용'
        }
    },
    methods: {
        countUp() {
            this.count++;
        }
    }   
}
</script>
<style>
    html {
	width: 80%;
	margin-right: auto;
	margin-left: auto;
	font-family: sans-serif;
	font-size: 16px;
	line-height: 1.5;
}

p{
    font-size:30px;
}

input,button,select,optgroup,textarea {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

button {
	cursor: pointer;
	padding: 6px 12px;
	border-radius: 6px;
	color: #fff;
	border: 2px;
	background-color: #007bff;
	transition: background-color .2s
}
button:hover {
	background-color: #0069d9;
}
button:active {
	background-color: #003c7c;
}
button:disabled {
	opacity: .5;
	pointer-events: none;
}

input {
	padding: 6px 12px;
	border-radius: 6px;
	color: #495057;
	border: 2px solid #ced4da;
}

textarea {
	width: 500px;
	height: 200px;
}
select {
	border: 2px solid #ced4da;
}

</style>