<template>
    <div id="parent">
        작성자: <input type="text" v-model="writer" id="writer"><br>
        제목: <input type="text" v-model="sub" id="subject"><br>
        글 내용: <input type="text" v-model="content" id="content"><br>
        <button v-on:click="insertContent">작성</button>
    </div>
    <br>
    <hr>
    <div class="input-table">
        <table id = "IC">
            <tr>
                <td>작성자</td><td>제목</td><td>글내용</td><td>작성시간</td><td>조회수</td>
            </tr>
        </table>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                writer:' ',
                sub:' ',
                content:' ',
                regtime:' ',
                hits:' '
            }
        },
        methods: {
            insertContent(){
                this.writer = document.getElementById('writer').value;
                this.sub = document.getElementById('subject').value;
                this.content = document.getElementById('content').value;
                this.regtime = new Date().toISOString().substring(0,10);
                this.hits = 0;

                const td1 = document.createElement('td');
                const td2 = document.createElement('td');
                const td3 = document.createElement('td');
                const td4 = document.createElement('td');
                const td5 = document.createElement('td');
    
                const IC = document.getElementById('IC');
                const tr = document.createElement('tr');
                const input=IC.appendChild(tr);
                input.appendChild(td1).innerHTML = this.writer;
                input.appendChild(td2).innerHTML = this.sub;
                input.appendChild(td3).innerHTML = this.content;
                input.appendChild(td4).innerHTML = this.regtime;
                input.appendChild(td5).innerHTML = this.hits;

                this.writer=' ';
                this.sub=' ';
                this.content=' ';
                this.regtime=' ';
                this.hits = ' ';
            }
        }
    }
</script>
<style>
    *{
        margin : 0;
        padding : 0;
    }

    #parent {
        width:100%;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
    }

    #input-table {
        width:100%;
        text-align: center;
        justify-content: center;
    }
    
</style>